body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.square{
    width: 80px;
    height: 80px;
    display: inline-block;
    border: 1px solid black;
    font-size: 30px;
    line-height: 50px;
    text-align: center;
    padding: 0;
    color: black;
    background-color: white;
    vertical-align: top;
}
.game-board{
    width: 250px;
}
.game{
    display: -webkit-flex;
    display: flex;
    margin-top: 1em;
}
.row{
    display: -webkit-flex;
    display: flex
}
.vs-view{
    text-align: center;
    cursor: pointer;
}
.App {
  text-align: center;
}

.success{
  color : green;
}

